//
// Forms
// --------------------------------------------------

// - - - - - - - - - - - - - - - - -
// Page Search Input
.page-search {

    max-width: 999px;
    
    // Input
    input {
        padding-left: 4rem !important;
        padding-right: rem !important;
        color: $dark;
        background-color: $light;
        font-size: 1.5rem;
        font-weight: 300;
        border: none;
        transition: all $transition-default;
        box-shadow: none;
       
        &::placeholder {
            color: $gray-l1;
        }

        // Focus
        &:focus {
            background-color: lighten($light, 1);
            box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 2px 14px rgba(0, 0, 0, 0.06), 0px 2px 15px rgba(0, 0, 0, 0.12);
        }
    }

    // Search icon
    i {
        font-size: 1.5rem;
        transition: all $transition-default;
    }

    // Focus within
    &:focus-within {
        i {
            color: $gray-l2;
        }
        
        input::placeholder {
            color: $dark;
        }
    }


}

// - - - - - - - - - - - - - - - - -
// Form Control
.form-control {
    &:focus {
        // color: #4b566b;
        // background-color: #fff;
        border-color: $accent;
        box-shadow: inset 0 1px 2px transparent, 0 0.375rem 0.625rem -0.3125rem rgba($secondary, 0.15);
}
}